body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

.container {
  width: 100%;
  margin: 0 auto;
  padding-left: 20px;
  padding-right: 20px;
}

@media screen and (min-width: 480px) {
  .container {
    width: 480px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
    padding-left: 32px;
    padding-right: 32px;
  }
}

@media screen and (min-width: 1280px) {
  .container {
    width: 1280px;
    padding-left: 15px;
    padding-right: 15px;
  }
}

html {
  box-sizing: border-box;
}

*, :before, :after {
  box-sizing: inherit;
}

p, h1, h2, h3, h4, h5, h6 {
  margin: 0;
}

.list {
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
  list-style: none;
}

img {
  max-width: 100%;
  display: block;
}

.box {
  justify-content: center;
  align-items: stretch;
  gap: 10px;
  margin-bottom: 15px;
  display: flex;
}

.timer {
  justify-content: center;
  gap: 15px;
  margin-top: 20px;
  display: flex;
}

.field {
  flex-direction: column;
  align-items: center;
  display: flex;
}

.button-css {
  text-align: center;
  background-color: #fff;
  border: 1px solid #000;
  border-radius: 4px;
  margin-top: 30px;
  padding: 6px 22px;
  display: block;
}

.button-css:hover, .button-css:focus {
  color: #fafafa;
  background-color: #3a8be7;
  box-shadow: 0 3px 1px #0000001a, 0 1px 2px #00000014, 0 2px 2px #0000001f;
}

.value {
  font-size: large;
  font-weight: 700;
}

a {
  color: #212121;
  text-decoration: none;
}

.button-style {
  color: #fafafa;
  cursor: pointer;
  background-color: #0c5685;
  border: none;
  border-radius: 4px;
  margin: auto;
  padding: 6px;
}

.input-style {
  width: 100%;
  height: 40px;
  border: 1px solid #21212133;
  border-radius: 4px;
  margin-bottom: 10px;
  font-size: 20px;
}

form {
  flex-direction: column;
  justify-content: center;
  display: flex;
}

.cat-thumb {
  margin-top: 40px;
}

@media screen and (min-width: 1280px) {
