    margin-top: 120px;
  }
}

.btn-thumb {
  width: 100%;
  height: 50px;
  justify-content: center;
  gap: 10px;
  margin-top: 20px;
  padding-left: 20px;
  padding-right: 20px;
  display: flex;
}

@media screen and (min-width: 1280px) {
  .btn-thumb {
    max-width: 300px;
    gap: 15px;
    display: flex;
  }
}

.button-switch {
  letter-spacing: .1em;
  color: #fafafa;
  background-color: #000;
  border: none;
  border-radius: 4px;
  flex-grow: 1;
  justify-content: center;
  align-items: center;
  font-family: raleway, sans-serif;
  font-size: 20px;
  font-weight: 500;
  line-height: 1.2;
  display: flex;
}

@media screen and (min-width: 1280px) {
  .button-switch {
    height: 48px;
    color: #fafafa;
    cursor: pointer;
    background-color: #000;
    gap: 16px;
    display: flex;
  }
}

body {
  width: 100%;
  position: absolute;
  overflow: hidden;
}

.giphy-cat {
  width: auto;
  pointer-events: none;
  position: relative;
  transform: translateX(-10px);
}

@media screen and (min-width: 1280px) {
  .giphy-cat {
    pointer-events: none;
    transition-property: transform;
    transition-duration: 1s;
    transition-timing-function: linear;
    position: relative;
    transform: translateX(-10px);
  }
}

.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

